body {
  font-family: var(--primary-font-family), sans-serif;
  color: var(--primary-text-color);
  &.modal-open {
    overflow: hidden;
  }
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
a {
  text-decoration: none;
  font-style: normal;
  //color: $accent-color;
}
p {
  margin: 0;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  color: var(--primary-section-title-color);
}
